@import "../../sizes.scss";
@import "../../colors.scss";
@import "../../elements-styles.scss";

.content {
    @include page-content-settings;

    position: relative;

    h1 {
        margin: 15px 0 36px;

        font-family: 'Ferry';
        font-weight: 900;
        font-size: 40px;
        line-height: 48px;
        letter-spacing: 0.04em;
    }

    h2 {
        font-family: 'Ferry';
        font-weight: 900;
        font-size: 30px;
        line-height: 36px;
        letter-spacing: 0.02em;

        margin-bottom: 17px;
    }

    h4 {
        font-weight: 400;
        font-size: 20px;
        line-height: 24px;
        letter-spacing: 0.02em;
    }

    .picture {
        position: absolute;
        top: -30px;
        left: 730px;
    }

    .report {
        margin-top: 127px;

        .count {
            font-size: 18px;
            line-height: 22px;
            letter-spacing: 0.02em;
            color: $gray;
        }
    }

    .documents {
        display: flex;
        flex-direction: column;
        margin-bottom: 109px;

        h2 {
            margin-bottom: 58px;
        }

        .documents__wrapper {
            display: flex;
            flex-wrap: wrap;
            row-gap: 32px;
            justify-content: space-between;
        }

        .show_more_button {
            @include button;

            width: 305px;
            margin: 38px auto 0;
        }
    }

    @include respond-to(laptop) {
        .picture {
            left: 50vw;
        }
    }

    @include respond-to(tablet) {
        h1 {
            font-size: 35px;
            line-height: 40px;
        }

        .picture {
            position: static;
            width: 350px;
            height: 233.73px;
        }

        .report {
            margin-top: 60px;
        }
    }

    @include respond-to(mobile) {
        h1 {
            width: 105%;
            font-size: 28px;
            line-height: 34px;
            letter-spacing: 0.01em;

            margin-bottom: 21px;
        }

        h2 {
            font-size: 28px;
            line-height: 34px;
            letter-spacing: 0.01em;
        }

        h4 {
            width: 115%;
            font-size: 18px;
            line-height: 22px;
            letter-spacing: 0em;
        }

        .report {
            .count {
                letter-spacing: 0.01em;
            }
        }
    }
}